import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { Company, FrontendLogger, UnreliableVendor } from '@biz-nest/commerce-client';
import {
  AppNameService,
  EventbusService, ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';

export abstract class UnReliableVendorListBase {
  posting = false;
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<Company>(
    Company,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (unreliableVendor: Company) => this.viewVendor(unreliableVendor)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (unreliableVendor: Company) => this.removeFromList(unreliableVendor)
    }
  ];

  unreliableVendors?: UnreliableVendor;

  constructor(
    protected windowService: WindowService,
    protected toastr: ToastrService,
    protected router: Router,
    protected eventbus: EventbusService,
    protected apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  async init() {
    await this.getUnreliableVendors();
    if (this.unreliableVendors?.flaggedCompanies && this.unreliableVendors?.flaggedCompanies?.length > 0) {
      this.listCompose.filterProps = {
        propFilter: {
          _id: this.unreliableVendors?.flaggedCompanies.map((val) => val.company) || []
        }
      };

      this.listCompose.getDataArr();
    }
    this.mainBarTitle.title = 'Unreliable Companies';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Efficiently manage and comprehensively monitor your unreliable companies,
      empowering you to maintain financial clarity and make informed decisions
      regarding your fiscal responsibilities and opportunities for savings.`
    });

    scrollToTop(this.windowService);
  }

  async getUnreliableVendors() {
    try {
      const { dataArr } = await UnreliableVendor.getAll();

      this.unreliableVendors = dataArr[0];
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  abstract removeFromList(company: Company): void;

  viewVendor(company: Company) {
    this.router.navigate(['/view-company' + company._id]);
  };

  destroy(): void {
    this.listCompose.cleanup?.();
  }
}
