import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { Company, UnreliableVendor } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  AppNameService, EventbusService, MainBarTitleService,
  UnReliableVendorListBase, WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import {
  AdminDateFilterFieldComponent
} from '../../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-un-reliable-vendor-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent
  ],
  templateUrl: './un-reliable-vendor-list.component.html',
  styleUrl: './un-reliable-vendor-list.component.scss'
})
export class UnReliableVendorListComponent
  extends UnReliableVendorListBase
  implements OnInit, OnDestroy {
  deleteModalOptions = {
    showModal: false,
    modalTitle: 'Remove Vendor From List',
    modalMsg: 'Are you sure you want to remove this item?',
    modalCancelButton: true,
    modalCancelText: 'Cancel',
    modalConfirmText: 'Remove',
    modalConfirmButton: true
  };

  company!: Company;

  constructor(
    protected override windowService: WindowService,
    protected override toastr: ToastrService,
    protected override router: Router,
    protected override eventbus: EventbusService,
    protected override apNme: AppNameService,
    public override mainBarTitle: MainBarTitleService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(windowService, toastr, router, eventbus, apNme, mainBarTitle, metaTagService, titleService);
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnDestroy(): void {
    this.destroy();
  }

  removeFromList(company: Company) {
    this.company = company;
    this.toggleModalDelete();
  }

  toggleModalDelete() {
    this.deleteModalOptions.showModal = !this.deleteModalOptions.showModal;
  }

  async onModalDeleteConfirm(event: boolean) {
    if (!event) {
      return;
    }

    try {
      await UnreliableVendor.modify(this.company._id, 'red', 'remove');
      this.toastr.success('Vendor removed from red list successfully');

      this.listCompose.getDataArr();
    } catch (error) {
      FrontendLogger.error(error);
      this.toastr.error('Failed to remove vendor from red list');
    }
  }
}
